.footer {
    background-color: $color-bcg-footer;
    height: 100px;
    width: 100%;
    padding-top: 28px;
    padding-bottom: 29px;
    text-align: center;
    @media screen and (min-width: 768px) {
      height: 79px;
      padding-top: 30px;
      padding-bottom: 30px;
    }
    
    &__text {
    color: $color-footer;
    line-height: calc(16 / 14);
    letter-spacing: 0.03em;
    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: calc(19 / 16);
      margin-right: 7px;
        }
    }

    &__container {
    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: center;
        }
    }
    
    &__link {
    padding-top: 10px;
    padding-bottom: 10px;
    color: $color-footer;
    line-height: calc(16 / 14);
    letter-spacing: 0.03em;
    cursor: pointer;

    &:hover,
    &:focus {
      color: $color-accent;
        }
    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: calc(19 / 16);
      padding: 0;
        }
    }
    
    &__sub {
    display: flex;
    color: $color-footer;
    line-height: calc(16 / 14);
    letter-spacing: 0.03em;
    align-items: center;
    justify-content: center;
    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: calc(19 / 16);
        }
    }
    
    &__icon {
    margin-left: 10px;
    margin-right: 10px;
  }
}